body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #28f1ff;
}

/* Header Styles */
.header {
  background-color: #282c34;
  color: white;
  padding: 20px;
  text-align: center;
}

.title {
  margin: 0;
  font-size: 2rem;
}

.nav {
  margin-top: 20px;
}

.nav-list {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: center;
}

.nav-item {
  margin: 0 10px;
}

.nav-item a {
  text-decoration: none;
  color: rgb(40, 176, 72);
  font-weight: bold;
  transition: color 0.3s ease;
}

.nav-item a:hover {
  color: #61dafb;
}

/* Section Styles */
section {
  background-color: rgb(225, 68, 167);
  padding: 40px 20px;
  margin: 20px;
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(93, 31, 168, 0.1);
}

section h2 {
  margin-top: 0;
}

/* Footer Styles */
footer {
  background-color: #282c34;
  color: rgb(38, 190, 213);
  text-align: center;
  padding: 20px;
}
